var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("哈莉·奎茵")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Harley Quinn（Harleen Frances Quinzel）是一部虚构角色，出现在DC Comics出版的漫画书中。这个角色由Paul Dini和Bruce Timm创作，并于1992年9月首次出现在蝙蝠侠：动画系列中。她后来出现在DC Comics的蝙蝠侠漫画书中，角色的首部漫画书出现在The Batman Adventures＃12（1993年9月） ）。在她的描绘中，她被描绘成医生精神病学家和心理学家。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Harley Quinn经常是小丑的帮凶和爱好者，她在Gotham City的Arkham Asylum担任实习精神病学家时遇到过，Joker是一名患者。她的名字是一个名为“ Harlequin ” 的剧本，这个角色起源于commedia dell'arte。这个角色已经与猫女和毒藤常春藤的同伴交战了好几次，这三人被称为高谭市警笛。众所周知，毒药常春藤是哈利的亲密朋友和反复出现的盟友，甚至在最近的漫画中被描绘成她的女朋友。自 “新52 ”以来，她现在被描绘成一个反英雄，并将她的过去留作了超级恶魔。然而，她有时仍被描述为超级恶棍。Harley Quinn也被描述为自杀小队的成员。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Harley Quinn首次出现在DC动画宇宙的蝙蝠侠：动画系列剧集“ Joker's Favor ”中，原本应该是动画的等同于一个步行角色; 一些警察被一个跳出蛋糕的人劫持为人质，并且决定让小丑自己这样做太奇怪了，尽管他最终还是这样做了。因此，他们为小丑创造了一个女性搭档; 她会成为他的爱情兴趣。肥皂剧“ 我们的生活日”的前明星阿琳·索尔金（Arleen Sorkin）出现在一个关于那个穿着小丑的系列中的梦幻序列中服装; 他们用这个场景作为奎因的灵感来源。自从大学毕业后与Sorkin成为朋友，他将自己的个性方面融入角色。昆也通过相互女性朋友的“暴风雨但非暴力的关系”的启发，根据蒂姆。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        1994年的图画小说 “蝙蝠侠冒险：疯狂的爱”叙述了角色的起源故事。由Dini和Timm编写和绘制，漫画书以蝙蝠侠：动画系列的风格和连续性讲述。它描述了医学博士Harleen Frances Quinzel博士作为Arkham Asylum 精神病学家，他爱上了小丑，并成为他的帮凶和一次又一次的女友。这个故事得到了广泛赞誉，并获得了Eisner和Harvey年度最佳单曲奖。New Batman Adventures系列改编了Mad Love 作为1999年的同名剧集。这是第二部